$(document).ready(function() {
  $(".menu-burger").click(function() {
    $(".humburger-menu").animate({
      left: '0'
    },500);
  });
  $(".close-window").click(function() {
    $(".humburger-menu").animate({
      left: '-320px'
    },500);
  });

  (function carouselModel() {
    $(".circle div").map((index, elem) => {
      if(index === 1) {
        showModel(index);
        $(elem).addClass('active-image');
      }
      $(elem).click(function() {
        $(".circle div").removeClass('active-image');
        $(".carousel-item").removeClass('show-item')
        $(this).addClass('active-image');
        showModel(index);
      })
    })
  })();
  
  function showModel(index_active) {
    $(".carousel-item").map((index, elem) => {
      if(index === index_active) {
        $(elem).addClass('show-item');
      }
    })
  };

  (function carouselPhoto() {
    $(".carousel-photo figure").map(item => {
      $(".circle-photo").append("<div></div>");
    })
    $(".circle-photo div").map((index, elem) => {
      if(index === 0) {
        showPhoto(index);
        $(elem).addClass('active-image');
      }
      $(elem).click(function() {
        $(".circle-photo div").removeClass('active-image');
        $(".carousel-photo figure").removeClass('show-item')
        $(this).addClass('active-image');
        showPhoto(index);
      })
    })
  })()

  function showPhoto(index_active) {
    if($(".carousel-photo").width() < 641) {
      $(".carousel-photo figure").map((index, elem) => {
        $(elem).addClass('hide-item');
        if(index === index_active) {
          $(elem).addClass('show-item'); 
        }         
      });
    } 
  };
  
  
  $(".block figure").hover(
    function() {
      const head = $(this).find('h4').text();
      const height = ($(this).height() < 431) ? $(this).height() : $(this).height() + 70;
      const width = $(this).width();

      $(this).append(`<section class='hover'>
                        <h4>${head}</h4>
                        <p>Bei uns finden Sie alles, was das Herz von
                          Fans, Sammlern und allen Freunden der
                          grossen und kleinen Flugzeuge höher shlagen lässt
                        </p>
                        <button>
                          <img src="../../static/img/air-freight.png" alt="">
                          <a href="#">${head}</a>
                        </button>
                      </section>`)
      $(".hover").height(height);
      $(".hover").width(width);        
    },
    function() {
      $(this).find(".hover").remove();
    }
  );

  $(".photo figure").hover(
    function() {
      const src = $(this).find('img').attr('src');
      console.log(src);
      $(this).find("img").css("opacity", "0");
      $(this).append(`<div class="hover-photo">
                        <figure>
                          <img src="../../${src}" alt="">
                          <div class="save-instagram">
                              <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 50 50" width="50px" height="50px">
                                <path style="line-height:normal;text-indent:0;text-align:start;text-decoration-line:none;text-decoration-style:solid;text-decoration-color:#000;text-transform:none;block-progression:tb;isolation:auto;mix-blend-mode:normal" d="M 16 3 C 8.8324839 3 3 8.8324839 3 16 L 3 34 C 3 41.167516 8.8324839 47 16 47 L 34 47 C 41.167516 47 47 41.167516 47 34 L 47 16 C 47 8.8324839 41.167516 3 34 3 L 16 3 z M 16 5 L 34 5 C 40.086484 5 45 9.9135161 45 16 L 45 34 C 45 40.086484 40.086484 45 34 45 L 16 45 C 9.9135161 45 5 40.086484 5 34 L 5 16 C 5 9.9135161 9.9135161 5 16 5 z M 37 11 A 2 2 0 0 0 35 13 A 2 2 0 0 0 37 15 A 2 2 0 0 0 39 13 A 2 2 0 0 0 37 11 z M 25 14 C 18.936712 14 14 18.936712 14 25 C 14 31.063288 18.936712 36 25 36 C 31.063288 36 36 31.063288 36 25 C 36 18.936712 31.063288 14 25 14 z M 25 16 C 29.982407 16 34 20.017593 34 25 C 34 29.982407 29.982407 34 25 34 C 20.017593 34 16 29.982407 16 25 C 16 20.017593 20.017593 16 25 16 z" font-weight="400" font-family="sans-serif" white-space="normal" overflow="visible"/>
                              </svg>
                          </div>
                        </figure>
                      </div>`);
    }, function() {
      $(this).find(".hover-photo").remove();
      $(this).find("img").css("opacity", "1")
    }
  )
});


